:root {
  --black: #090909;
  --darkgray: #1c1c1c;
  --gray: #5a5a5a;
  --white: #FFFDFD;
  --darkgrayclear: #1c1c1c66;

  --pink1: #F72585;
  --purple1: #B5179E;
  --purple2: #7209B7;
  --purple3: #560BAD;
  --purple4: #480CA8;
  --blue1: #3A0CA3;
  --blue2: #3F37C9;
  --blue3: #4361EE;
  --blue4: #4895EF;
  --blue5: #4CC9F0;
  --lightgreen: #80ed99;
}

html,
body {
  width: 100%;
  height: 100%;
  background-color: var(--darkgray);
  color: var(--white);
}

button {
  cursor:pointer;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  /* display: flex;
  flex-direction: column; /* Make it a vertical flex container if needed */
  /* background-color: var(--gray); */ 
  /* color: var(--white); */

}

